.reviews {
    padding-top: 80px;
    padding-bottom: 86px;
}

.reviews-text {
    position: relative;
    z-index: 10;
    padding: 0 15px;
    @include font(500, 12px, 31px);
    color: var(--black);
    text-align: center;
    width: 157px;
    margin: 0 auto;
    background-image: repeating-linear-gradient(
    transparent,
    transparent 29px,
    #E1E1E1 29px,
    #E1E1E1 30px
  );

    &::before {
        position: absolute;
        content: "";
        display: block;
        top: -12px;
        left: 17px;
        z-index: -1;
        width: 54px;
        height: 40px;
        background-image: url(../images/quotes.svg);  
  }
}

.slider-item {
    text-align: center;
}

.reviews-img {
    margin: 0 auto;
    margin-bottom: 29px;
}

.reviews-wrapper {
    width: 280px;
    margin: 0 auto;
    background-color: var(--reviews-background);
    padding-top: 43px;
    padding-bottom: 20px;
}

.location-text {
    @include font(500, 14px, 26px);
    color: var(--black);

    &::before {
        position: absolute;
        content: "";
        display: block;
        left: 50%;
        top: 30px;
        background-image: url(../images/group.svg);
        width: 32px;
        height: 4px;
        transform: translateX(-50%);
    }
}

.reviews-box {
    position: relative;
    padding-top: 52px;
    padding-bottom: 30px;
}

.slick-list {
  overflow: hidden;
}

.slick-track {
  display: flex;
}

.slick-dots {
  list-style: none;
  font-size: 0;
  display: flex;
  align-items: center;
  justify-content: center;

  & li {
    margin: 0 9px;
    }
    
    & button {
    font-size: 0;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: var(--reviews-elements-color);
    border: none;
    outline: transparent;
    }
}

.slick-active button {
  background-color: transparent;
  background-image: url(../images/home.svg);
  background-position: center;
  background-size: cover;
  border-radius: 0;
}

@media screen and (min-width: 768px) {
  .reviews {
    padding-top: 119px;
    padding-bottom: 119px;
  }

  .reviews-text {
    @include font(500, 14px, 30px);
    width: 457px;
    padding: 0;

    &::before {
      top: -16px;
      left: -25px;
    }
  }

  .reviews-wrapper {
    width: 620px;
    padding-top: 47px;
    padding-bottom: 28px;
  }

  .location-text {
    @include font(500, 16px, 30px);

    &::before {
      top: 24px;
    }
  }

  .reviews-img {
    margin-bottom: 19px;
  }

  .reviews-box {
    padding-top: 42px;
    padding-bottom: 35px;
  }
}

@media screen and (min-width: 1200px) {
  .reviews {
    padding-top: 123px;
    padding-bottom: 132px;
  }

  .reviews-text {
    @include font(500, 16px, 30px);
    width: 710px;
    padding: 0 15px;

    &::before {
      left: -10px;
    }
  }

  .reviews-wrapper {
    width: 926px;
    padding-bottom: 32px;
  }

  .location-text {
    @include font(500, 20px, 37px);

    &::before {
      top: 15px;
    }
  }

  .reviews-img {
    margin-bottom: 15px;
  }

  .reviews-box {
    padding-top: 34px;
  }
}